	module.exports = {"data":{"site":{"siteMetadata":{"title":"Nobody Listens Anyway","author":"Justin Ball"}},"markdownRemark":{"id":"/Users/jbasdf/projects/justinball/src/posts/2010-01-01-undefined-method-assert_no_difference-and-assert_difference/index.md absPath of file >>> MarkdownRemark","html":"<p>I've been upgrading some old code and noticed that my unit tests started spitting out a lot of errors like this:</p>\n<pre><code class=\"ruby\">\nundefined method `assert_no_difference'\n</pre></code>\n<p>and</p>\n<pre><code class=\"ruby\">\nundefined method `assert_difference'\n</pre></code>\n<p>It turns out that I needed to change my base classes from <strong>Test::Unit::TestCase</strong> to <strong>ActiveSupport::TestCase</strong>:</p>\n<pre><code class=\"ruby\">\nclass UserTest < Test::Unit::TestCase\n  # stuff\nend\n</pre></code>\n<p>changes to:</p>\n<pre><code class=\"ruby\">\nclass UserTest < ActiveSupport::TestCase\n  # stuff\nend\n</pre></code>","frontmatter":{"title":"undefined method `assert_no_difference&#8217; and `assert_difference&#8217;","date":"January 01, 2010","image":null}}},"pathContext":{"path":"/undefined-method-assert_no_difference-and-assert_difference"}}

